<template>
  <div class="post-wrap">
    <h3>功能介紹</h3>
    <p>使用 Vue.js 的資料綁定功能，即時將使用者輸入的金額，轉換為計算後的價格。亦可根據合約內容，手動調整是否包含勞健保，並附上費用的中文數字版本，讓使用者可以直接抄寫於報價單，無須額外再查詢寫法，對於記不住中文數字寫法的使用者來說實在便利。</p>

    <p>本網站有兩種計算機可供使用，第一種為「報價計算機」，輸入最終實領金額後，計算出報價金額。第二種為「最終實領計算機」，輸入報價金額後，計算出最終實領金額。使用者可依據自己的需求，選擇要使用哪一種計算機，也可兩者都使用，比較金額有無出入。</p>

    <h4>報價計算機</h4>
    <p>輸入 <span class="inline-wrap">實領金額</span> 推導出 <span class="inline-wrap">報價金額</span>。</p>

    <div class="block-wrap">
        <p>最終實領(支領淨額) + 健保費 + 所得稅 = 報價(支領金額)</p>
        <p>X/(1 -0.1 -0.0211) - 0.1X/(1 -0.1 -0.0211) - 0.0211X/(1 -0.1 -0.0211) = X</p>
    </div>
    <br>
    <ul>
        <li>報價（支領金額） <span class="inline-wrap">X/(1 -0.1 -0.0211)</span> (輸出)</li>
        <li>健保費（四捨五入） <span class="inline-wrap">0.0211X/(1 -0.1 -0.0211)</span></li>
        <li>所得稅（四捨五入） <span class="inline-wrap">0.1X/(1 -0.1 -0.0211)</span></li>
        <li>最終實領（支領淨額） <span class="inline-wrap">X</span> (輸入)</li>
    </ul>

    <!-- image: 1 column 450px width -->
    <div class="img-wrap column-1">
        <img src="@/assets/images/work/quote_calculation/feature_1.png" />
    </div>


    <h4>最終實領計算機</h4>
    <p>輸入 <span class="inline-wrap">報價金額</span> 推導出 <span class="inline-wrap">實領金額</span>。</p>

    <div class="block-wrap">
        <p>報價(支領金額) - 健保費 - 所得稅 = 最終實領(支領淨額)</p>
        <p>X -0.0211X -0.1X = (1 -0.1 -0.0211)X</p>
    </div>
    <br>
    <ul>
        <li>報價（支領金額） <span class="inline-wrap">X</span> (輸入)</li>
        <li>健保費（四捨五入） <span class="inline-wrap">0.0211X</span></li>
        <li>所得稅（四捨五入） <span class="inline-wrap">0.1X</span></li>
        <li>最終實領（支領淨額） <span class="inline-wrap">(1 -0.1 -0.0211)X</span> (輸出)</li>
    </ul>

    <!-- image: 1 column 450px width -->
    <div class="img-wrap column-1">
        <img src="@/assets/images/work/quote_calculation/feature_2.png" />
    </div>


    <h3>介面</h3>

    <h4>1920*1080 電腦版（截圖）</h4>
    <div class="device-wrap pc-dev-wrap" style="--device-w:1920; --device-h:1080;">
        <div class="dev-outter-wrap">
            <div class="dev-inner-wrap">
                <img src="@/assets/images/work/quote_calculation/1920x1080.png" />
            </div>
        </div>
    </div>

    <h4>375*667 手機版（截圖）</h4>
    <div class="col-1-mobile-wrap">
        <div class="device-wrap mobile-dev-wrap" style="--device-w:375; --device-h:667;">
            <div class="dev-outter-wrap">
                <div class="dev-inner-wrap">
                    <img src="@/assets/images/work/quote_calculation/375x667.png" />
                </div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
// import jquery
import $ from "jquery";
window.$ = $;

export default {
    name: 'SpQuoteCalculation',
    mounted() {
      /* if not mac, hide scroll */
      this.isMac = navigator.platform.toUpperCase().indexOf('MAC') >= 0; // not found = -1
      let $dev_outter_wrap = $('.dev-outter-wrap');
      if ( !this.isMac && $dev_outter_wrap.length > 0 ) {
        $dev_outter_wrap.addClass('hide-scroll');
      }

      /* tabs feature */
      $('.tab-nav').on('click', function(){
        let tab_panel = $(this).parent('.tab-nav-wrap').attr('data-panel');
        
        let tab = $(this).attr('data-tabs');
        $(this).siblings('.active').removeClass('active');
        $(this).addClass('active');
    
        let $tab_content_wrap = $('.tab-content-wrap').filter(`[data-panel="${tab_panel}"]`);
        $tab_content_wrap.find('.active').removeClass('active');
        $tab_content_wrap.find(`[data-tabs="${tab}"]`).addClass('active');

        $('html, body').animate({
            'scrollTop': $(this).offset().top - 70
        }, 300);
      });
    }
}
</script>

